<template>
    <div class="list row">
        <div class="col-md-6">
            <h4>Banners List</h4>
            <ul>
                <li v-for="(banner, id) in banners" :key="id">
                    <router-link :to="{
                            name: 'banner-details',
                            params: { banner: banner, id: banner.id }
                        }">
                        <div class="banner">
                            <a :href="banner.targeturl">
                                <img :width="banner.width" :height="banner.height" :src="banner.imgsrc"/>
                            </a>
                        </div>
                    </router-link>
                </li>
            </ul>
        </div>
        <img src="../assets/img/login.jpg" alt="Login"/>
        <img src="../assets/img/banners/acura.jpg"/>
        <img src="../assets/img/banners/alfa-romeo.jpg"/>
        <img src="../assets/img/banners/aston_martin.jpg"/>
        <img src="../assets/img/banners/audi.jpg"/>
        <img src="../assets/img/banners/bentley.jpg"/>
        <div class="col-md-6">
            <router-view @refreshData="refreshList"></router-view>
        </div>
    </div>
</template>

<script>
    import http from "../http-common";
    export default {
        name: "banners-list",
        data() {
            return {
                banners: []
            };
        },
        methods: {
            /* eslint-disable no-console */
            retrieveBanners() {
                http
                    .get("/getAllBanners")
                    .then(response => {
                        this.banners = response.data; // JSON are parsed automatically.
                        console.log(response.data);
                    })
                    .catch(e => {
                        console.log(e);
                    });
            },
            refreshList() {
                this.retrieveBanners();
            }
            /* eslint-enable no-console */
        },
        mounted() {
            this.retrieveBanners();
        }
    };
</script>

<style scoped>

</style>


